import Axios from 'axios'
import React,{ useState } from 'react'
import { useSelector} from 'react-redux'
import SingleComment from './SingleComment'
import ReplyComment from './ReplyComment'
function Comment(props) {
    
    const videoId = props.postId;
    const user = useSelector(state =>state.user);
    const [commentValue, setcommentValue] = useState("")
    const handleClick = (event) => {
        setcommentValue(event.currentTarget.value)
    }
    const onSubmit = (event) => {
        //  
        event.preventDefault();
        
        const variables = {
            content: commentValue,
            writer: user.userData._id, //  .
            postId: videoId
        }
        
        Axios.post('/api/comment/saveComment',variables)
        .then(response => {
           
            if(response.data.success){
                
                props.refreshFunction(response.data.result)
                setcommentValue("");
            }else {
                alert('comment not save')
            }
        })
    }
    return (
        <div>
            <br />
            <p> {props.commentLists?props.commentLists.length:"0"}</p>
            <hr /> 


            {/* Comment Lists */}
            {props.commentLists && props.commentLists.map((comment,index) => (
                    (!comment.responseTo &&
                        <React.Fragment key={index}>
                            <SingleComment refreshFunction={props.refreshFunction} comment={comment} postId={videoId}/>
                            <ReplyComment refreshFunction={props.refreshFunction} parentCommentId={comment._id} postId={videoId} commentLists={props.commentLists}/> 
                        </React.Fragment>//    
                    )
            ))}
            

            {/* Root Comment Form */}
            <br />
            {user &&
            <form style={{ display:'flex'}} onSubmit={onSubmit}>
                <textarea
                    style={{ width: '100%' , borderRadius:'5px' , resize:'none'}}
                    onChange={handleClick}
                    value={commentValue}
                    placeholder="   ."

                
                />
                <br />
                <button style={{ width:'20%' ,height:'52px'} } onClick={onSubmit}></button>

            </form>
            }
           
        </div>
    )
}

export default Comment
