import React from 'react'
import { Descriptions} from 'antd'

function MovieInfo(props) {
    let {movie} = props;
    return (
        <Descriptions title=" " bordered>
            <Descriptions.Item label="">{movie.original_title}</Descriptions.Item>
            <Descriptions.Item label="">{movie.release_date}</Descriptions.Item>
            <Descriptions.Item label="">${movie.revenue}</Descriptions.Item>
            <Descriptions.Item label="">{movie.runtime} </Descriptions.Item>
            <Descriptions.Item label="">
                {movie.vote_average}
            </Descriptions.Item>
            <Descriptions.Item label="">{movie.vote_count} </Descriptions.Item>
            <Descriptions.Item label="">{movie.status==='Released'?"":""}</Descriptions.Item>
            <Descriptions.Item label="">{movie.popularity}</Descriptions.Item>
        </Descriptions>
        
    )
}

export default MovieInfo
