import React,{useState} from 'react'
import { Typography, Button, Form, message, Input} from 'antd'
import Axios from 'axios';
import {useSelector} from 'react-redux';

const { Title} = Typography;

function UploadChartPage(props) {
    const user = useSelector(state => state.user);
    const [ChartTitle, setChartTitle] = useState("")
    const [names, setNames] = useState([]);
    const [inputText, setInputText] = useState('');
    const [nextId, setNextId] = useState(0);
    
      const onChange = e => setInputText(e.target.value);
      const onClick = () => {
        const chk=inputText.split("/")
        if(inputText === null || inputText.match("/")===false ||isNaN(chk[1])!==false){
            return alert(' ')
        }
        const nextNames = names.concat({
          id: nextId,
          name: chk[0],
          value: chk[1],
        });
        setNextId(nextId + 1);
        setNames(nextNames);
        setInputText('');
      };
    
      const nameList = names.map(name => (
        <Input style={{width:"150px"}} key={name.id} value={name.name+"/"+name.value} onDoubleClick={() => onRemove(name.id)}/>
        ));
    const onTitleChange = (e) => {
        setChartTitle(e.currentTarget.value)
    }
    const onRemove = id => {
        const nextNames = names.filter(name => name.id !== id);
        setNames(nextNames);
      };
    const onKeypresshandler = e => {
        if(e.key==="Enter"){
            onClick();
        }
    }
    const onSubmit = (e) =>{
        e.preventDefault();
        if(!ChartTitle || names.length===0){
            return alert(' ')
        }
        const variables = {
            writer: user.userData._id,
            title: ChartTitle,
            data: names
           


        }
        Axios.post('/api/chart/uploadChart',variables)
        .then(response => {
            if(response.data.success){
                message.success('  .')
                setTimeout(() => {
                    props.history.push('/chart')
                }, 3000);
            }else{
                alert('   .')
            }
        })

    }
    return (
        <div style={{ maxWidth:'700px', margin:'2rem auto'}}>
            <div style={{ textAlign:'center', marginBottom:'2rem'}}>
                <Title level={2}> </Title>
            </div> 
                <Form onSubmit={onSubmit}>

            <label></label>
            <Input
                onChange={onTitleChange}
                value={ChartTitle}
            />
            <br />
            <br />
            <label>/</label>  <br /> <b style={{color:'red'}}>/      ex. 1125/180 </b>
            <Input style={{width:"150px"}} value={inputText} onChange={onChange} onKeyPress={onKeypresshandler}/>
            &nbsp;<Button onClick={onClick}></Button>
            <div>{nameList}</div>
            { names.length!==0 &&
                <b style={{color:'red'}}>    </b>}
            <br />
            <br />
            <Button type="primary" size="large" onClick={onSubmit}>
                
            </Button>
                </Form>
            
            
        </div>
    )
}

export default UploadChartPage